<template>
    <div style="width: 100%;">
        <div class="container">
            <!-- start -->
            <div class="aboutGoods">
                <div class="goods">
                    <img :src="datas.infoImg">
                    <div class="info">
                        <p>{{datas.goodsName}}</p>
                        <span>:</span>
                        <br>
                        <span>{{getHour}}  {{getMinute}} </span>
                    </div>
                </div>
            </div>
            <!-- end -->

            <!-- start -->
            <p class="notice">
                 48 
            </p>
            <!-- end -->

            <!-- start -->
            <p class="label"></p>
            <textarea v-if="datas.isEdit" v-model="datas.applyMessage" id="applyMessage"></textarea>
            <textarea v-else v-model="datas.applyMessage" id="applyMessage" readonly></textarea>

            <div class="line"></div>

            <p class="label"></p>
            <input class='inputClass' type="text" placeholder="" :value="datas.userName" readonly>

            <p class="label"></p>
            <div id="addressMessage">{{datas.address}}</div>

            <p class="label"></p>
            <input class='inputClass' type="number" placeholder="" :value="datas.postCodes" readonly>

            <p class="label"></p>
            <input class='inputClass' type="number" placeholder="" :value="datas.tel" readonly>

            <div class="editAddress" v-show="datas.isEdit"></div>

            <!-- end -->

            <!-- start -->
            <div class="verifyBtn" @click='test'></div>
            <!-- end -->
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      datas: {
        isEdit: true,
        infoImg:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        goodsName: "Misfit SHINE ",
        deadline: 1525942800000,
        applyMessage:
          "Mavic Air  Mavic Pro  Mavic Air ",
        userName: "",
        address: " C#401dasdasd",
        postCodes: 518000,
        tel: 12345678901
      },
      hourTimer: null,
      hour: 0,
      minuteTimer: null,
      minute: 0
    };
  },
  computed: {
    getHour: function() {
      this.getHourFun();
      return this.hour;
    },
    getMinute: function() {
      this.getMinuteFun();
      return this.minute;
    }
  },
  methods: {
    test() {
      console.log(this.datas.applyMessage);
    },
    // data
    getData(data) {},
    getHourFun() {
      let _this = this;

      if (!_this.hourTimer) {
        clearInterval(_this.hourTimer);
        _this.hourTimer = setInterval(function() {
          hourF();
        }, 30000);
      }

      function hourF() {
        let now = Date.parse(new Date());
        let hour = Math.floor((_this.datas.deadline - now) / (3600 * 1000));
        if (hour < 0) {
          _this.hour = 0;
          return;
        }
        _this.hour = hour;
      }
      hourF();
    },
    getMinuteFun() {
      let _this = this;

      function minuteF() {
        let now = Date.parse(new Date());
        let tempMin = Math.floor((_this.datas.deadline - now) / 1000 / 60);
        let hour = Math.floor((_this.datas.deadline - now) / (3600 * 1000));
        let remain = tempMin - hour * 60;
        if (remain < 0) {
          _this.minute = 0;
          return;
        }
        _this.minute = remain;

        // let now = Date.parse(new Date());
        // let totalMinute = Math.floor((_this.datas.deadline - now) / 1000) / 60;
        // let day = Math.floor((_this.datas.deadline - now) / 1000 / 86400);
        // let hour =
        //   Math.floor((_this.datas.deadline - now) / 1000 / 3600) -
        //   Math.floor((_this.datas.deadline - now) / 1000 / 86400) * 24;
        // if (totalMinute < 0) {
        //   _this.minute = 0;
        //   return;
        // }
        // _this.minute = Math.floor(totalMinute - day * 24 * 60 - hour * 60);
      }

      if (!_this.minuteTimer) {
        clearInterval(_this.minuteTimer);
        _this.minuteTimer = setInterval(function() {
          minuteF();
        }, 1000);
      }

      minuteF();
    }
  },
  mounted() {
    // textare 
    (function() {
      var tx = document.getElementsByTagName("textarea");
      if (tx.length == 0) {
        return;
      }
      for (var i = 0; i < tx.length; i++) {
        tx[i].setAttribute(
          "style",
          "height:" + tx[i].scrollHeight + "px;overflow-y:hidden;"
        );
        tx[i].addEventListener("input", OnInput, false);
      }

      function OnInput() {
        this.style.height = "auto";
        this.style.height = this.scrollHeight + "px";
      }
    })();
  }
};
</script>

<style lang="less" scoped>
@m: 750/375;
/*  */
textarea,
input {
  border: 0;
}
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: auto;
}

.container .label {
  font-size: 16px * @m;
  color: #b9b9b9;
  margin-bottom: 10px * @m;
}

/*  */

.container .aboutGoods .goods {
  height: 114px * @m;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.8);
  border-radius: 6px * @m;
  margin-bottom: 20px * @m;
  padding: 12px * @m;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.container .aboutGoods .goods img {
  width: 135px * @m;
  height: 90px * @m;
  border-radius: 8px * @m;
  margin-right: 16px * @m;
}

.container .aboutGoods .goods .info p {
  width: 158px * @m;
  font-size: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 10px * @m;
}

.container .aboutGoods .goods .info span {
  font-size: 13px * @m;
  color: #c6c6c6;
}

.container .notice {
  font-size: 13px * @m;
  color: #c6c6c6;
  margin-bottom: 30px * @m;
}

/*  */

#applyMessage {
  width: 100%;
  font-size: 16px * @m;
  color: #0f2540;
  margin-bottom: 16px * @m;
  resize: none;
}

#addressMessage {
  /* width: 100%;
        font-size: 16px;
        margin-bottom: 20px;
        resize: none; */
  font-size: 16px * @m;
  margin-bottom: 20px * @m;
}

.line {
  border: 1px solid transparent;
  border-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' height='2px'%3E%3Crect fill='%23EFEFEF' width='100%25' height='50%25'/%3E%3C/svg%3E")
    2 2 stretch;
  margin-bottom: 30px * @m;
}

.inputClass {
  font-size: 16px * @m;
  margin-bottom: 20px * @m;
}

.editAddress {
  width: 120px * @m;
  height: 40px * @m;
  margin-top: 10px * @m;
  background: #e9eef4;
  border-radius: 100px * @m;
  color: #a6b1bf;
  font-size: 14px * @m;
  line-height: 40px * @m;
  text-align: center;
  /* display: none; */
}

/*  */

.verifyBtn {
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  width: 100px * @m;
  height: 40px * @m;
  color: #fff;
  line-height: 40px * @m;
  text-align: center;
  margin: 30px * @m auto;
}
</style>



