// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from '../App'
import moment from 'moment'

import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import 'html5-reset/assets/css/reset.css'



// s
//
import applyForTest from '@/views/apply-for-test' //
import verifyAddress from '@/views/verify-address' // 

import successList from '@/views/apply-success-list' //
import verifyApplyInfo from '@/views/verify-apply-info' //
import mytestdetail from '@/views/my-test-detail' //()
import hotTestProductList from '@/views/test-product' // 
import hotTestExperience from '@/views/test-experience' // 

import testProductDetail from '@/views/test-product-detail' //
import testExperienceReportDetail from '@/views/test-experience-report-detail' //

// 
import idleIndex from '@/views/idle-index' //
import idleDetail from '@/views/idle-detail' //

// 
import activityIndex from '@/views/activity-index'                              //
import activityOfflineDetail from '@/views/activity-offline-detail'             //
import activityForNightClubDetail from '@/views/activity-for-night-club-detail' //
import activityForTailPKDetail from '@/views/activity-for-tail-pk-detail'       //pk

Vue.use(Router)
Vue.filter('dateFormat', function (value, type) { //
  return moment(value).format(type);
});

const router = new Router({
  // mode:'history',
  routes: [{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/applyForTest',
      name: 'applyForTest',
      component: applyForTest
    }, {
      path: '/successList',
      name: 'successList',
      component: successList
    },
    {
      path: '/verifyApplyInfo',
      name: 'verifyApplyInfo',
      component: verifyApplyInfo
    },
    {
      path: '/mytestdetail',
      name: 'mytestdetail',
      component: mytestdetail
    },
    {
      path: '/hotTestProductList',
      name: 'hotTestProductList',
      component: hotTestProductList
    },
    {
      path: '/hotTestExperience',
      name: 'hotTestExperience',
      component: hotTestExperience
    },
    {
      path: '/verifyAddress',
      name: 'verifyAddress',
      component: verifyAddress
    },
    {
      path: '/testProductDetail',
      name: 'testProductDetail',
      component: testProductDetail
    },
    {
      path: '/testExperienceReportDetail',
      name: 'testExperienceReportDetail',
      component: testExperienceReportDetail
    },
    {
      path: '/idleIndex',
      name: 'idleIndex',
      component: idleIndex
    },
    {
      path: '/idleDetail',
      name: 'idleDetail',
      component: idleDetail
    },
    {
      path: '/activityIndex',
      name: 'activityIndex',
      component: activityIndex
    },
    {
      path: '/activityOfflineDetail',
      name: 'activityOfflineDetail',
      component: activityOfflineDetail
    },
    {
      path: '/activityForNightClubDetail',
      name: 'activityForNightClubDetail',
      component: activityForNightClubDetail
    },
    {
      path: '/activityForTailPKDetail',
      name: 'activityForTailPKDetail',
      component: activityForTailPKDetail
    },



  ]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



