<template>
    <div style="width: 100%;">
        <div class="container">
          <!-- start -->
          <div class="main">
              <ul class="testList" id="testList" v-if="datas.length>0">
                  <li v-for="(item,index) in datas" :key="index">
                      <div class="testItem">
                          <img :src='item.productImg'>
                          <div class="testInfo">
                              <p class="testName">{{item.productName}}</p>
                              <p class="testStatus">{{item.testStatusStr}}</p>
                          </div>
                      </div>
                      <div class="btn" v-if="item.testStatus !== 0 && item.testStatus !==1 ">
                          <div class="examine" ></div>
                          <!-- 0  , 1  ,  2 ,  3 , 4  -->
                          <div class="affirm" v-if="item.testStatus==2"></div>
                          <div v-else-if="item.testStatus==3" class="pastDue"></div>
                          <div v-else-if="item.testStatus==4" class="submitTest"></div>
                      </div>
                  </li>
              </ul>

              <!--  start -->
              <div class="nullData" id="nullData" v-else>
                  <img class="nullDataImg" src="../assets/image/test_null@3x.png">
                  <p></p>
                  <p></p>
              </div>
              <!--  end -->

          </div>
          <!-- end -->
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      datas: [
        {
          productImg:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          productName: "Misfit SHINE ",
          testStatusStr: "",
          testStatus: 0
        },
        {
          productImg:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          productName: "apple watch 3",
          testStatusStr: " 02  01 ",
          testStatus: 1
        },
        {
          productImg:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          productName: "Misfit SHINE asdasdasdasd",
          testStatusStr: " 48 ",
          testStatus: 2
        },
        {
          productImg:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          productName: "Misfit SHINE asdasdasdasd",
          testStatusStr: "  48  ",
          testStatus: 3
        },
        {
          productImg:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          productName: "Misfit SHINE asdasdasdasd",
          testStatusStr: "  ",
          testStatus: 4
        }
      ]
    };
  },
  methods: {
    // data
    getData(data) {}
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
@m: 750/375;
/*  */
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
}

/*  */

.main {
  margin-top: 30px * @m;
}

#testList {
  display: block;
}

.main .testList li {
  position: relative;
  width: 100%;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.8);
  border-radius: 6px * @m;
  margin-bottom: 20px * @m;
  clear: both;
}

.main .testList .testItem {
  height: 114px * @m;
  background: #ffffff;
  border-radius: 6px * @m;
  padding: 12px * @m;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.main .testList .testItem img {
  width: 135px * @m;
  height: 90px * @m;
  border-radius: 8px * @m;
  margin-right: 16px * @m;
}

.testList .testItem .testInfo {
  width: 158px * @m;
  padding: 6px * @m 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.testList .testItem .testInfo .testName {
  width: 158px * @m;
  font-size: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.testList .testItem .testInfo .testStatus {
  width: 158px * @m;
  font-size: 13px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  color: #c6c6c6;
}

.testList .btn {
  padding: 0 12px * @m;
  display: flex;
  justify-content: flex-end;
  flex-wrap: nowrap;
  height: 50px * @m;
}

.testList .btn .examine {
  border: 1px solid #979797;
  display: inline-block;
  background: #e9eef4;
  border-radius: 100px * @m;
  box-sizing: border-box;
  padding: 11px * @m 18px * @m;
  width: 122px * @m;
  height: 36px * @m;
  color: #a6b1bf;
  line-height: 14px * @m;
  font-size: 14px * @m;
  margin-right: 20px * @m;
}

.testList .btn .affirm {
  display: inline-block;
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3),
    0 20px * @m 31px * @m 0 rgba(0, 0, 0, 0.13);
  border-radius: 100px * @m;
  color: #fff;
  box-sizing: border-box;
  height: 38px * @m;
  padding: 11px * @m 22px * @m;
  border: 1px solid #9adaff;
  line-height: 16px * @m;
}

.testList .btn .pastDue {
  display: inline-block;
  color: #b9b9b9;
  background: #efefef;
  border-radius: 100px * @m;
  padding: 11px * @m 22px * @m;
  box-sizing: border-box;
  height: 38px * @m;
  border: 1px solid #efefef;
  line-height: 16px * @m;
}

.testList .btn .submitTest {
  display: inline-block;
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3),
    0 20px * @m 31px * @m 0 rgba(0, 0, 0, 0.13);
  border-radius: 100px * @m;
  color: #fff;
  box-sizing: border-box;
  height: 38px * @m;
  padding: 11px * @m 22px * @m;
  border: 1px solid #9adaff;
  line-height: 16px * @m;
}

/*  */

#nullData {
  //   display: none;
}

.nullData .nullDataImg {
  display: block;
  width: 200px * @m;
  height: 232px * @m;
  margin: 0 auto;
  margin-top: 120px * @m;
  margin-bottom: 30px * @m;
}

.nullData p:nth-child(2) {
  font-size: 16px * @m;
  color: #435771;
  text-align: center;
  margin-bottom: 12px * @m;
}

.nullData p:nth-child(3) {
  font-size: 13px * @m;
  color: #c6c6c6;
  text-align: center;
}
</style>



