// loading
.hideLoad {
  display: none !important;
}

.showLoad {
  display: block !important;
}

.shade {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 99;
}

.loading {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

