<template>
  <div style="width: 100%;">
    <div class="container">
      <!-- start -->
      <div class="aboutGoods">
        <div class="goods">
          <img :src='datas.imgSrc'>
          <div class="info">
            <p>{{datas.productName}} </p>
            <span> {{datas.quota}} </span>
            <span> {{datas.person}} </span>
          </div>
        </div>
      </div>
      <!-- end -->

      <!--  start -->
      <p class="lable"></p>
      <textarea id="applyReason" v-model="textareaValue" autofocus placeholder=""></textarea>
      <!--  end -->
    </div>
    <!-- start -->
    <div class="nextBtn" @click='checkStr'></div>
    <!-- end -->
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      datas: {
        productName: "Misfit SHINE asdasdasd",
        imgSrc:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        quota: 3,
        person: 718
      },
      textareaValue: ""
    };
  },
  methods: {
    checkStr() {
      let len = this.textareaValue.replace(/[^\x00-\xff]/g, "__").length;
      if (len > 1000) {
        alert("500");
        return;
      } else if (len < 40) {
        alert('20')
      } else {
        console.log("pass");
      }
    },

    // data
    getData(data) {}
  },
  mounted() {
    // textare 
    (function() {
      var tx = document.getElementsByTagName("textarea");
      for (var i = 0; i < tx.length; i++) {
        tx[i].setAttribute(
          "style",
          "height:" + tx[i].scrollHeight + "px;overflow-y:hidden;"
        );
        tx[i].addEventListener("input", OnInput, false);
      }

      function OnInput() {
        this.style.height = "auto";
        this.style.height = this.scrollHeight + "px";
      }
    })();
  }
};
</script>

<style lang="less" scoped>
@m: 750/375;
/*  */

.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  // border: 1px solid transparent;
  font-size: 14px * @m;
  height: 595px * @m;
  overflow: auto;
}

/*  */

.container .aboutGoods {
  margin: 30px * @m 0;
}

.container .aboutGoods .goods {
  height: 108px * @m;
  background: #ffffff;
  // box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.8);
  // border-radius: 6px * @m;
  margin-bottom: 30px * @m;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.container .aboutGoods .goods img {
  width: 162px * @m;
  height: 108px * @m;
  border-radius: 6px * @m;
  margin-right: 16px * @m;
}
.container .aboutGoods .goods .info {
  padding: 3px* @m 0;
}
.container .aboutGoods .goods .info p {
  width: 158px * @m;
  font-size: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 16px * @m;
}

.container .aboutGoods .goods .info span {
  display: block;
  font-size: 13px * @m;
  color: #c6c6c6;
  margin-bottom: 6px * @m;
}

/*  */

.lable {
  color: #bcbcbc;
  margin-bottom: 15px * @m;
}

#applyReason {
  resize: none;
  width: 100%;
  font-size: 18px * @m;
  height: auto;
  border: 0;
}

/*  */

.nextBtn {
  position: relative;
  bottom: 0;
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  width: 100px * @m;
  height: 40px * @m;
  color: #fff;
  line-height: 40px * @m;
  text-align: center;
  margin: 0px auto;
  font-size: 16px * @m;
  margin-top: 10px * @m;
}
</style>



